'use strict';

$(document).ready(function() {

	yardsPerAttempt = function() {
			return this.yards/this.attempts+'yards';
		}

	function Quarterback(firstname, lastName, team) {
		this.firstName = firstName;
		this.lastName = lastName;
		this.team = team;
		this.attemps = 0;
		this.completions = 0;
		this.yards = 0;
		this.touchdowns = 0;
		this.interceptions = 0;
		this.sacks = 0;
		this.longestCompletion = 0;
		this.yardsPerAttempt = yardsPerAttempt();
		this.completionPercentage = function() {
			return Math.round(this.completions / this.attempts * 100)+'%';
		}
		this.addAttempt = function(type, yards) {
			type.toLowerCase();
			if(type === 'touchdown') {
				this.touchdown++;
				this.attempts++;
				this.yards += yards;
				this.completions++;
			}
			if(type === 'complete') {
				this.completions++;
				this.attempts++;
				this.yards += yards;
			}
			if(type === 'incomplete') {
				this.attempts++
			}
			if(type === 'sack') {
				this.sacks++;
				this.yards -= yards;
			}
			if(type === 'interception') {
				this.interception++;
			}
		}
	}

	function runningBack(firstname, lastName, team) {
		this.firstname = firstname;
		this.lastName = lastName;
		this.team = team;
		this.attempts = 0;
		this.yards = 0;
		this.over20 = 0;
		this.touchdowns = 0;
		this.firstDowns = 0;
		this.fumbles = 0;
		this.longestRush = 0;
		this.yardsPerAttempt = yardsPerAttempt();
		this.addAttempt = function(type, yards) {
			type.toLowerCase();
			if(yards > 20) {
				this.over20++;
			}
			if(type === 'fumble') {
				this.fumbles++;
				this.attempts++;
			}
		}
	}















});